import PropTypes, {arrayOf, shape, string} from 'prop-types'
import React from 'react';
import {Grid, GridColumn, GridContainer} from "../Grid";
import Cta from "../Cta";
import {Title} from "../Title";
import "./news-feed.scss";

const renderArticleElement = (item, index) => {
  return (
    <GridContainer key={index} className="news-feed__wrapper">
      <Grid className="news-feed__item">
        <GridColumn columns="6" className="news-feed__left">
          <Title variant="bold">
            {item.title}
          </Title>

          <Cta to={item.url} variant="readmore-yellow">
            read more
          </Cta>
        </GridColumn>

        <GridColumn columns="6" className="news-feed__right">
          <div className="news-feed__right-summary">{item.summary}</div>

          <div className="news-feed__right-footer">
            <span>{item.date}</span> - Written by {item.author}
          </div>
        </GridColumn>
      </Grid>
    </GridContainer>
  );
};

function NewsFeed({articles, tagFilter}) {
  const articleElements = articles
    .filter(article => article.tag.includes(tagFilter) || !tagFilter)
    .map((article, index) => renderArticleElement(article, index));

  return (
    <div className="news-feed">{articleElements}</div>
  )
}

NewsFeed.propTypes = {
  articles: arrayOf(
    shape({
      title: string.isRequired,
      summary: string,
      date: string,
      author: string,
      url: string,
    }),
  ).isRequired,
  tagFilter: PropTypes.string,
};

export default NewsFeed;
