import React, {useState} from 'react'
import {arrayOf, string, shape} from 'prop-types'
import {Title} from "../../components/Title";
import NewsTags from "../../components/NewsTags";
import NewsFeed from "../../components/NewsFeed";
import {Grid, GridColumn, GridContainer} from "../../components/Grid";
import "./news-section.scss";

function NewsSection({tags, articles}) {
  const [tagFilter, setTagFilter] = useState(false);

  return (
    <GridContainer className="news-section">
      <Grid variant="centered">
        <GridColumn columns="11">
          <Grid>
            <Title variant="lightLead" className="news-section__title">
              Our <br/>
              <span>latest news.</span>
            </Title>

            <NewsTags tags={tags} tagFilter={tagFilter} setTagFilter={setTagFilter} />

            <NewsFeed articles={articles} tagFilter={tagFilter} />
          </Grid>
        </GridColumn>
      </Grid>
    </GridContainer>
  )
}

NewsSection.propTypes = {
  tags: arrayOf(string).isRequired,
  articles: arrayOf(
    shape({
      title: string.isRequired,
      summary: string,
      date: string,
      author: string,
      url: string,
    }),
  ).isRequired,
};

export default NewsSection
