import PropTypes, {arrayOf} from 'prop-types'
import React from 'react';
import "./news-tags.scss";
import cx from "classnames";

const renderTagElement = (tag, tagFilter, setTagFilter) => {
  return (
    <button
      key={tag}
      className={cx("news-tags__button", {
        [`news-tags__button--highlight`]: tagFilter === tag,
      })}
      onClick={() => setTagFilter(tag)}>
      {tag}
    </button>
  );
};

function NewsTags({ tags, tagFilter, setTagFilter }) {
  const tagElements = tags.map((tag) => renderTagElement(tag, tagFilter, setTagFilter));

  return (
    <div className="news-tags">
      <button
        className={cx("news-tags__button news-tags__button--all", {
          [`news-tags__button--highlight`]: !tagFilter,
        })}
        onClick={() => setTagFilter(false)}>
        All
      </button>
      {tagElements}
    </div>
  )
}

NewsTags.propTypes = {
  tags: arrayOf(PropTypes.string).isRequired,
  tagFilter: PropTypes.bool.isRequired,
  setTagFilter: PropTypes.func.isRequired,
};

export default NewsTags;
